<template>
<div class="permission">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/loginSuccess/homepage' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/loginSuccess/permission' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!--  // -->
      <el-button type="primary" style="float:right;margin-bottom:15px" @click="dialogVisible = true"></el-button>
      <el-dialog title="" :visible.sync="dialogVisible" size="tiny" :before-close="handleClose">
        <el-form>
            <el-form-item label="">
                <el-input v-model="juese"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="yonghu"></el-input>
            </el-form-item>
        </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="dialogVisible = false,add()" > </el-button>
            </span>
      </el-dialog> 

        <!--  //--> 
      <el-table  :data="tableData3">
        <el-table-column prop="juese" label="" ></el-table-column>
        <el-table-column prop="yonghu" label=""></el-table-column>
        <el-table-column label="" prop="caozuo">
            <template scope="scope">
           <!--  // -->
          <el-button size="small" type="text"v-if="scope.$index!=0" @click="permission=true,getData(scope.$index)"><i class="el-icon-setting"></i></el-button> 
              <el-dialog :title="setpermission() " :visible.sync="permission" size="tiny" :before-close="handleClose">
                    <el-checkbox-group v-model="checkedoption">
                        <el-checkbox v-for="Option in Options" :label="Option" :key="Option" style="margin:15px;">{{Option}}</el-checkbox>
                    </el-checkbox-group>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="permission = false"> </el-button>
                    <el-button type="primary" @click="permission = false" > </el-button>
                </span>
              </el-dialog> 

               <!--  // -->
                <el-button size="small" type="text" v-if="scope.$index!=0" @click="menber=true,getData(scope.$index)"><i class="el-icon-setting"></i></el-button>
                   <el-dialog :title="setmenber()" :visible.sync="menber" size="tiny" :before-close="handleClose">
          <el-collapse accordion>
              <el-collapse-item>
                  <template slot="title">
                    <el-checkbox label=""></el-checkbox>
                  </template>
                    <div><el-checkbox label=""></el-checkbox></div>
                    <div><el-checkbox label=""></el-checkbox></div>
              </el-collapse-item>
              <el-collapse-item>
                  <template slot="title">
                    <el-checkbox label=""></el-checkbox>
                  </template>
                    <div><el-checkbox label=""></el-checkbox></div>
                    <div><el-checkbox label=""></el-checkbox></div>
              </el-collapse-item>
              <el-collapse-item>
                  <template slot="title">
                    <el-checkbox label=""></el-checkbox>
                  </template>
                    <div><el-checkbox label=""></el-checkbox></div>
                    <div><el-checkbox label=""></el-checkbox></div>
              </el-collapse-item>
              <el-collapse-item>
                  <template slot="title">
                    <el-checkbox label=""></el-checkbox>
                  </template>
                    <div><el-checkbox label=""></el-checkbox></div>
                    <div><el-checkbox label=""></el-checkbox></div>
              </el-collapse-item>
              <el-collapse-item>
                  <template slot="title">
                    <el-checkbox label=""></el-checkbox>
                  </template>
                    <div><el-checkbox label=""></el-checkbox></div>
                    <div><el-checkbox label=""></el-checkbox></div>
              </el-collapse-item>
          </el-collapse>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="menber = false"> </el-button>
                    <el-button type="primary" @click="menber = false" > </el-button>
                  </span>
                </el-dialog>

               <!-- // -->
                <el-button size="small" type="text" v-if="scope.$index!=0" @click="edit=true,getData(scope.$index)"><i class="el-icon-edit"></i></el-button> 
                <el-dialog :title="editing()" v-model="edit"  size="tiny" :before-close="handleClose">
                    <el-form    label-width="100px">
                        <el-form-item label="">
                            <el-input v-model="juse"></el-input>
                        </el-form-item>
                        <el-form-item label="" >
                            <el-input v-model="yonghu"></el-input>
                        </el-form-item>
                       
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="edit = false"> </el-button>
                        <el-button type="primary" @click="edited()"> </el-button>
                    </div>
                </el-dialog>

                <!-- // -->
                <el-button size="small" type="text" v-if="scope.$index!=0" @click=" delet=true,getData(scope.$index)"><i class="el-icon-delete"></i></el-button>
                <el-dialog :title="dele()" :visible.sync="delet" size="tiny" :before-close="handleClose">
                  <i style="font-size:50px;color:red;float:left;"class="el-icon-warning" ></i>
                  <div></div>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="delet = false"> </el-button>
                    <el-button type="primary" @click="delet = false,deleteRow()"> </el-button>
                  </span>
                </el-dialog> 

               <!--  // -->
                <el-button size="small" type="text" v-if="scope.$index==0" @click="own=true"></el-button>
            </template>
        </el-table-column>  
      </el-table>
      <el-row>
      <el-form style="margin-top:50px"> 

       <!--   -->
      <el-col :span="3"><div>
         <el-form-item >
             <el-select  v-model="selecting" placeholder="10/">
             <el-option label="10/" value="shi" class="selected"></el-option>
             <el-option label="20/" value="ershi"></el-option>
             <el-option label="30/" value="sanshi"></el-option>
             <el-option label="50/" value="wushi"></el-option>
             </el-select>
         </el-form-item>
         </div></el-col>

          <!--   --> 
       <el-col :span="3"><div>
       <div class="block" style="margin-top:2px;">
              <el-pagination layout="prev, pager,next ,jumper" :total="100"></el-pagination>
       </div> 
       </div></el-col>
     </el-form>
    </el-row>   
</div>
    </div>

</template>

<script type="ecmascript-6">
import {api} from '../../../global/api.js'
    const perOptions = ['', '', '', '',
                         '', '', '', '',
                         '', '', '',
                         '', '', '', ''
    ];
    export default{  
      data(){
        return {
          checkedoption:[],
          Options:perOptions,
          ind:"null",
          juse:"null",
          juese:"",
          yonghu:"",
          caozuo:"",
          selecting:"",
          dialogVisible: false,
          permission:false,
          menber:false,
          edit:false,
          own:false,
          delet:false,
          tableData3:null
          // tableData3:[
          // {
          //   juese:"",
          //   yonghu:""
          // },
          //   {
          //     juese:"",
          //   yonghu:"                "
            
          // },
          // {
          //   juese:"",
          //   yonghu:"           "
            
          // },
          // {
          //   juese:"",
          //   yonghu:"  "
            
          // },
          // {
          //   juese:"",
          //   yonghu:"     "
            
          // },
          // {
          //   juese:"",
          //   yonghu:"  "
            
          // }
          // ]
          }
    },
    mounted:function(){
        this.getJson();
    },
    methods:{
    	getJson(){ 
            this.$http.get(api.permission).then(
                function(response){
                    // alert("");
                    console.log(response.data);
                    this.tableData3=response.data.tableData3;
                    
                },function(){
                    alert("");
                })
        },
         //
        add:function(){
          this.tableData3.push({juese:this.juese,yonghu:this.yonghu})
        },
        
        //
        getData(index){
          this.ind=index;
          this.juse=this.tableData3[index].juese;
          this.yonghu=this.tableData3[index].yonghu;
         
        },
        
        //
        deleteRow(){
           this.tableData3.splice(this.ind,1); 
        },

        //
        setpermission(){
          var juese=this.juse;
          return "-"+juese;
          
        },

         //
        setmenber(){
          var juese=this.juse;
          return "-"+juese;
          
        },

         //
       editing(){
          var juese=this.juse;
          return "-"+juese;
          
       },

       //
        edited(){
          // var vm=this;
          this.tableData3[this.ind].juese=this.juse;
          this.tableData3[this.ind].yonghu=this.yonghu;
          this.edit=false;
         

        },

         //
        dele(){
          var juese=this.juse;
          return ""+juese+""
        },
        handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    
       },
      
  }
</script>

<style rel="stylesheet">

  .permission{
  	/*width:83.75%;*/
  	float: left;
    width:82%;
    padding:0 2% 30%;
    background:white; 
    position:relative;
    
  }  
  .permission .el-table{
    font-size: 15px;
    border-radius: 8px!important;
    box-shadow: 1px 1px  5px #eee!important;
  }
  .permission .el-table .el-button{
    padding-left:15px;
    font-size: 14px !important;
  }
  .permission .el-input__inner{
    border-radius: 0!important;
    height:28px!important;
   }
  .permission .el-table th{
    background:#eef1f6;
    border:solid thin #dfe6ec!important;
   } 
  .permission .el-table td{
    height: 70px!important;
   /* border:solid thin #dfe6ec!important;*/
      }
  .permission .selected{
       background:20a0ff; 
   }
</style>



