<template>
    <div class="scheduling">
        
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/loginSuccess/homepage'}">
          </el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <div exampleform-row style="margin-top:-10px">
            <el-input v-model="doctorName" placeholder=""  style="width: 200px;"></el-input>

            <el-date-picker v-model="selectData" type="date" placeholder="" >
            </el-date-picker>
            
            <el-button type="primary" icon="search"></el-button>
            <el-button type="primary" icon="edit" @click="add()"></el-button>        
            <el-button type="primary" icon="document"></el-button>
            <el-button type="primary" icon="delete" @click="delGroup" :disabled="this.sels.length === 0"></el-button>
            <!--disabledtrue,false--> 

        </div>

    <!--  -->
        <el-table :data="tableData"  border style="width: 100%"  highlight-current-row @selection-change="selsChange" ref="table">
      
            <el-table-column type="selection" width="55">
            </el-table-column>

             <el-table-column type="index" width="50">
             </el-table-column>

            <el-table-column prop="date"  label="" sortable width="180" >
            </el-table-column>
            
            <el-table-column prop="name" label="" width="180">
            </el-table-column>

            <el-table-column prop="appointment" label="" :formatter="formatter">
            </el-table-column>

            <el-table-column label="">

                <template scope="scope">
                    <el-button size="small" @click="setCurrent(scope.$index,scope)"></el-button>
                    <el-button size="small" type="danger" @click="deleteRow(scope.$index,tableData)"></el-button>
                </template>

            </el-table-column>
    
        </el-table>

    <!--  -->
        <el-form  style="clear:both"> 

             <el-col :span="3">
               <el-form-item style="width: 100px;height:28px;">
                   <el-select  placeholder="10/" >
                   <el-option label="10/" class="selected"></el-option>
                   <el-option label="20/"></el-option>
                   <el-option label="30/"></el-option>
                   <el-option label="50/"></el-option>
                   </el-select>
               </el-form-item>
             </el-col>

           <el-col :span="3">
             <div class="block">
                  <el-pagination layout="prev, next ,jumper" :total="50"></el-pagination>
             </div>
           </el-col> 

        </el-form>


      <!--  begin-->

      <el-dialog title="" v-model="dialogUpdateVisible"  >
   
          <el-form  :model="update"  ref="update" label-width="100px">

              <el-form-item label="">
                  <el-input v-model="update.date"></el-input>
              </el-form-item>
              <!-- <el-form-item label="" >
               <el-date-picker v-model="tableData[i].date" type="date" placeholder="" >
              </el-date-picker>
              </el-form-item> -->


              <el-form-item label="" >
                  <el-input v-model="update.name"></el-input>
              </el-form-item>
              <el-form-item label="" >
                  <el-input v-model="update.appointment"></el-input>
              </el-form-item>
          </el-form>

          <div slot="footer" class="dialog-footer">
              <el-button @click="dialogUpdateVisible = false"> </el-button>
              <el-button type="primary" @click="sure()"> </el-button>
          </div>
   
      </el-dialog>

      <!--  -->
      <el-dialog title="" v-model="addVisble" >
       
              <el-form id="#update" :model="update"   label-width="100px">
                  <el-form-item label="" >
                      <el-input v-model="update.date"></el-input>
                  </el-form-item>
                  <el-form-item label="" >
                      <el-input v-model="update.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" >
                      <el-input v-model="update.appointment"></el-input>
                  </el-form-item>
              </el-form>

              <div slot="footer" class="dialog-footer">
                  <el-button @click="addVisble = false"> </el-button>
                  <el-button type="primary" @click="addSure()"> </el-button>
              </div>
       
      </el-dialog>

    </div>
</template>

<script type="ecmascript-6">
  import {api} from '../../../global/api.js'

  export default{
       data() {
          return {
              i:0,
              selectData: '',
              doctorName:'',
              select:'',
              data:'',
              dialogUpdateVisible: false, //
              addVisble:false, //
              sels: [],//
              tableData: [{   
                date: '2016-05-02',
                name: '',
                appointment: '9.00-11.00'
             
              }, {
                
                date: '2016-05-04',
                name: '',
                appointment: '9.00-11.00'
               
              }, {
                
                date: '2016-05-01',
                name: '',
                appointment: '9.00-11.00'
                
              }, {
                
                date: '2016-05-04',
                name: '',
                appointment: '9.00-11.00'
                
              }, {
                
                date: '2016-05-03',
                name: '',
                appointment: '9.00-11.00'
               
              }],

              update:{

                date:'',
                name:'',
                appointment:''
              }               
          }
      },
      mounted:function(){
        this.getData();
      },
      methods: {
          // schedul.json
          getData(){ 
                    this.$http.get(api.schedual).then(
                        function(response){
                            // alert("");
                            console.log(response.data);
                            this.tableData=response.data.tableData;
                            
                        },function(){
                            alert("");
                        })

                    },    
          // 
          deleteRow(index, rows){
            rows.splice(index, 1)
          },

          // 
            setCurrent(index, rows){
              console.log(rows);
            this.i=index;//
            this.update.date=rows.row.date;
            this.update.name=rows.row.name;
            this.update.appointment=rows.row.appointment;
            this.dialogUpdateVisible=true;//
          },
          formatter(row, column) {
            return row.appointment;
          },
          filterTag(value, row) {
            return row.tag === value;
          },

          //
          sure(){       
          var vm = this;
          vm.dialogUpdateVisible=false;
          this.tableData[this.i].date=this.update.date;
          this.tableData[this.i].name=this.update.name;
          this.tableData[this.i].appointment=this.update.appointment;
          },

          //
          add(){
          this.update.date="";//
          this.update.name="";
          this.update.appointment="";
          var vm = this;
          vm.addVisble=true;         
          },

          //
          addSure(){
          var vm=this;
          this.addVisble=false;//
          this.tableData.unshift({date:vm.update.date,name:vm.update.name,appointment:vm.update.appointment});//push
          },

          selsChange: function (sels) {
            this.sels = sels;
          },
          //
          delGroup: function () {
            var ids = this.sels.map(item => item.id).toString();
            this.$confirm('', '', {
              type: 'warning'
            }).then(() => {
              // this.listLoading = true;
              //NProgress.start();
              let para = {ids: ids};
              reqBatchDeleteBook(para).then((res) => {
                this.listLoading = false;
                //NProgress.done();
                this.$message({
                  message: '',
                  type: 'success'
                });
                // this.getBooks();
              });
            }).catch(() => {

            });
          },
          handleCurrentChange(row, event, column) {  
            this.$refs.table.toggleRowSelection(row);  
          },
       
        }
         
     
    }
</script>

<style rel="stylesheet">
    .el-breadcrumb{
      padding: 10px 0 20px 0;
    }
    .scheduling .el-input__inner{
      border-radius: 0!important;
      height:28px!important;
     }
    .scheduling .el-pagination{
      padding-top: 4px;
     }
     .scheduling .el-table{
      padding-top: 10px;
      padding-bottom: 10px;
     }
     .scheduling{
      float:left;
      width:82%;
      padding:1% 2% 30%;
      background:#fff;
      margin-top: -1px;
    }
    
    .scheduling .el-input{
      margin-right: 12px;
    }

    .scheduling .el-button{
      margin-top: 8px;
      margin-left: 12px;
  }

</style>


